/* stylelint-disable scss/dollar-variable-colon-space-before */
/* ==========================================================================
   SCSS Variables
   ========================================================================== */

/*
   Typography variables
   ========================================================================== */

$font-family-sans-serif       : 'Nunito Sans', 'Open Sans', Verdana, 'Helvetica Neue', Helvetica, Arial, sans-serif;
$font-family-serif            : Georgia, 'Times New Roman', Times, serif;
$heading-font                 : 'Helvetica Neue','Segoe UI',Arial,sans-serif;
$code-font                    : Menlo, Monaco, 'Ubuntu Mono', 'Droid Sans Mono', Consolas, 'Courier New', Courier, monospace;
$base-font                    : $font-family-sans-serif;
$caption-font                 : $base-font;
$alt-font                     : $heading-font;

$doc-font-size                : 12;
$doc-line-height              : 12;

$border-radius                : 3px;

/*
   Color variables
   ========================================================================== */


$white                        : #fff;
$black                        : #000;
$black-nav                    : #282828;
$purple                       : #483366;
$purple-light                 : #6B547B;
$purple-lighter               : #b996fc;
$purple-highlight             : #f6f1ff;
$gray                         : #999;
$gray-medium                  : #ddd;
$gray-light                   : #aaa;
$gray-lighter                 : #eee;
$gray-dark                    : #555;
$gray-darker                  : #222;

$body-color                   : #ebebeb;

$text-color                   : $gray-dark;
$caption-color                : mix($white, $text-color, 25%);

$base-color                   : #343434;
$comp-color                   : complement($base-color);
$border-color                 : #ddd;
$link-color                   : $purple;
$link-hover-color             : darken($link-color, 15%);
$link-hover-decoration        : underline;


$primary-color                : $black;
$brand-primary                : $purple-light;
$success-color                : #5cb85c;
$warning-color                : #f0ad4e;
$danger-color                 : #d9534f;
$info-color                   : #5d9cec;
$disabled-color               : #434a54;

$table-border-color           : $border-color;
$table-border                 : 1px solid $table-border-color;
$table-background             : $body-color;
$table-header-color           : lighten($table-background, 10);
$table-hover-color            : darken($table-background, 2);
$table-stripe-color           : darken($table-background, 4);
$table-stripe-color-hover     : darken($table-stripe-color, 5);

$facebook-color               : #3b5998;
$flickr-color                 : #ff0084;
$foursquare-color             : #0cbadf;
$google-plus-color            : #dd4b39;
$instagram-color              : #4e433c;
$linkedin-color               : #4875b4;
$pinterest-color              : #cb2027;
$rss-color                    : #fa9b39;
$tumblr-color                 : #2c4762;
$twitter-color                : #55acee;
$vimeo-color                  : #1ab7ea;
$youtube-color                : #ff3333;

/*
   Form variables
   ========================================================================== */

$form-border-color            : $border-color;
$form-border-color-hover      : darken($border-color, 10);
$form-border-color-focus      : $primary-color;
$form-border-radius           : $border-radius;
$form-box-shadow              : inset 0 1px 3px hsla(0, 0%, 0%, 0.06);
$form-box-shadow-focus        : $form-box-shadow, 0 0 5px rgba(darken($form-border-color-focus, 5), 0.7);
$form-font-family             : $base-font;
$form-font-size               : $doc-font-size;


/*
   Sliding menu navigation variables
   ========================================================================== */

$sliding-menu-border-color     : $brand-primary;
$sliding-menu-background       : $brand-primary;
$sliding-menu-color            : $brand-primary;
$sliding-menu-background-hover : $purple;
$sliding-menu-color-hover      : $black;
$sliding-menu-border           : 1px solid $sliding-menu-border-color;
$button-size                   : 30px;
$transition                    : 0.3s; // increase this to see the transformations in slow-motion
$sliding-menu-animation        : all 500ms cubic-bezier(0.86, 0.01, 0.77, 0.78);

/*
   Badge variables
   ========================================================================== */

$badge-background              : $primary-color;
$badge-dark-color              : $black;
$badge-danger-color            : $danger-color;
$badge-info-color              : $info-color;
$badge-warning-color           : $warning-color;
$badge-success-color           : $success-color;
$badge-font-color              : $white;


/*
   Masthead variables
   ========================================================================== */

$masthead-height               : 30px;
$footer-height                 : 50px;

/*
   hero variables
   ========================================================================== */

$hero-background-top           : $purple-light;
$hero-background-bottom        : #84BBD5;
$hero-background-angle         : 10deg;
